@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Axiforma Regular';
  src: url(/job-voter-app/static/media/AxiformaRegular.008758f5.otf) format("opentype");
}
.leaderTitle {
  font-family: 'Nunito', sans-serif;
  padding-bottom: 1em;
}
.searchBar {
  margin-bottom: 10px;
  outline: none;
  width: 70%;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2)!important;
  border-width: 1px;
}
.head3 {
  padding-bottom: 5em;
}
.border th,
td {
  border: 1px solid rgba(224, 224, 224, 1);
}
.border {
    width: 70%;
    margin-top: 20px;
  }
.lb2 {
    border: none;
    margin-right: 1em;
    margin-top: 1em;
    float: right;
  }
  .lb2:focus {
    outline: none !important;
    outline-offset: none !important;
    box-shadow: none !important;
  }
.card2 {
  text-align: center;
  margin: auto;
  align-items: center;
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-self: center;
  justify-content: center;
  background-color: white;
  width: 70%;
  padding-top: 50px;
  padding-bottom: 50px;
  border-radius: 0.5rem;
  border-width: 1px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2)!important;
  margin-bottom: 10px;
}

@media (max-width : 980px) {
  .leaderTitle {
    padding-bottom: 0.25em;
  }
  .card2 {
    margin-top: 0em;
    padding-top: 25px;
    width: 100%;
    border-radius: 0;
    margin-bottom: 0px;
  }
  .border {
    margin-top: 10px;
    width: 96%;
  }
  .searchBar {
    width: 92%;
  }
}

.card {

    background-color: white!important;
    height: 20rem;
    width: 30rem;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 0.5rem;
    border: 0 solid #e5e7eb!important;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2)!important;
}
.helper {
    display: inline-block;
    height: 100%;
    vertical-align: middle;
}

#container {
    width: 60px;
    height: 60px;
    align-self: center;
    background-color: white;
    margin-bottom: 2rem;
 }
 
 #container img {
    max-width: 100%;
    max-height: 100%;
    vertical-align: middle;
    display: inline-block;
 }
 .jobCompany {
    font-size: 2.25rem;
 }
 .jobSalary {
    font-size: 1.5rem;
    font-family: 'Nunito';
 }
.jobTitle {
    font-size: 1.25rem;
    color: black;
    font-family:'Nunito', 'Arial Narrow', Arial, sans-serif;
    text-decoration: none;
}

@media screen and (max-device-width: 980px) {
    #container {
        margin-bottom: 1rem;
    }
    .card {
        width: 100%;
        height: 13rem;
    }
    a {
        width: 100%;
    }
    .jobTitle {
        font-size: 1rem;
    }
    .jobCompany {
        font-size: 1.5rem;
    }
    .jobSalary {
        font-size: 1rem;
    }
}

@font-face {
    font-family: 'Axiforma Regular';
    src: url(/job-voter-app/static/media/AxiformaRegular.008758f5.otf) format("opentype");
  }
  @font-face {
    font-family: 'Axiforma Bold';
    src: url(/job-voter-app/static/media/AxiformaBold.4a328a71.otf) format("opentype");
  }
  body {
    background-color: #F3F4F6;
  }
  .App {
    text-align: center;
    background-color: #F3F4F6;
  }
  a:hover, a:visited, a:link, a:active {
    text-decoration: none;
    color: black;
  } 
  .container {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .voteButton {
    border: none;
    height: 100%;
    background-color: #F3F4F6;
  }
  .lb {
    float: right;
    margin-right: 1em;
  }
  .lb:focus {
    outline: none !important;
    outline-offset: none !important;
    box-shadow: none !important;
  }
  .head {
    padding-top: 1em;
  }
  .title {
    margin-top: 3em;
    padding-bottom: 2em;
    font-family: 'Nunito';
    font-weight: 800;
  }
  @media (max-width : 980px) {
    .container {
      flex-direction: column;
      align-items: center;
    }
    .title {
      padding-bottom: 1em;
      margin-right: 0.2em;
      margin-left: 0.2em;
      font-size: 30px;
    }
    .voteButton {
      width: 100%;
    }
  }
